import React, { useRef, useState } from 'react'
import { noop } from 'lodash'
import styled from 'styled-components'
import { useMeasure } from '@elliemae/ds-utilities/hooks'
import useDerivedStateFromProps from '@elliemae/ds-utilities/hooks/useDerivedStateFromProps'
import PropTypes from 'prop-types'
import { runAll } from '@elliemae/ds-utilities/utils'
import { EditableTitleComponent } from '../blocks'
import { PageHeaderTitle } from '../PageHeaderTitle/PageHeaderTitle'

const EditableContainer = styled.div`
  height: 32px;
  position: relative;
  overflow: hidden;
`

export const PageHeaderEditableTitle = ({
  children: value,
  editing: editingProp = false,
  onBlur = noop,
  onChange = noop,
  onFocus = noop,
  onMouseOut = noop,
  showMarker = false,
  ...rest
}) => {
  const [titleValue, setTitleValue] = useState(value)
  const [hovered, setHovered] = useState(false)
  const [editing, setEditing] = useDerivedStateFromProps(editingProp || false)
  const titleRef = useRef(null)
  const { width } = useMeasure(titleRef)
  return (
    <EditableContainer className={hovered || editing ? 'editable-title-component' : ''} editing={editing}>
      {(hovered || editing) && (
        <EditableTitleComponent
          {...rest}
          onBlur={runAll(() => setEditing(false), onBlur)}
          onChange={runAll(e => setTitleValue(e.target.value), onChange)}
          onFocus={runAll(() => setEditing(true), onFocus)}
          onMouseOut={runAll(() => setHovered(false), onMouseOut)}
          style={{ width: width - 2, minWidth: '260px' }}
          value={titleValue}
        />
      )}
      <div ref={titleRef} onMouseEnter={() => setHovered(true)}>
        <PageHeaderTitle showMarker={showMarker && !hovered && !editing}>
          {titleValue}
        </PageHeaderTitle>
      </div>
    </EditableContainer>
  )
}

PageHeaderEditableTitle.propTypes = {
  /** The title text*/
  children: PropTypes.string,
  /** Whether the title is editing or not*/
  editing: PropTypes.bool,
  /** Show error marker*/
  showMarker: PropTypes.bool,
}

export default PageHeaderEditableTitle
