import React from 'react'
import { PageHeaderSummaryWrapper } from '../blocks'

export const PageHeaderSummary = ({ value = null, label = '', ...rest }) => (

  <PageHeaderSummaryWrapper {...rest}>
    {value && <div className='ph-summary-value'>{value}</div>}
    {label && <div className='ph-summary-label'>{label}</div>}
    {(value || label) && <div className='ph-summary-space' />}
  </PageHeaderSummaryWrapper>
)
