import React from 'react'
import DSDropdownMenu from '@elliemae/ds-dropdownmenu'
import DSButton from '@elliemae/ds-basic/Button'
import ChevronSmallDown from '@elliemae/ds-icons/ChevronSmallDown'
import TitleWrapper from './TitleWrapper'


function DropdownTitle({
  titleComponent,
  onSelectOption,
  options,
  optionsMinWidth,
  selection,
  zIndex,
}) {
  return (
    <TitleWrapper>
      {titleComponent}
      <DSDropdownMenu
        minWidth={optionsMinWidth}
        onSelectMenuItem={onSelectOption}
        options={options}
        selection={selection}
        triggerComponent={(
          <DSButton
            buttonType='text'
            data-testid='page-header-dropdown-trigger-component'
            icon={<ChevronSmallDown />}
          />
        )}
        zIndex={zIndex}
      />
    </TitleWrapper>
  )
}

DropdownTitle.propTypes = {}

export default DropdownTitle
