/* eslint-disable no-shadow */
import React, { cloneElement } from 'react'
import PropTypes from 'prop-types'
import { aggregatedClasses } from '@elliemae/ds-classnames'
import DSBreadcrumbItem from './DSBreadcrumbItem'

const blockName = 'breadcrumb'

const DSBreadcrumb = ({
  containerProps = {},
  as: Component = 'nav',
  label = 'Breadcrumb',
  children,
  ...rest
}) => {
  const SafeComponent = ['nav', 'div', 'header', 'footer', 'main'].indexOf(Component) > -1 ? Component : 'nav'
  const NavContainer = aggregatedClasses(SafeComponent)(blockName)

  return (
    <NavContainer
      aria-label={label}
      data-testid='breadcrumb-nav-container'
      {...rest}
      {...containerProps}
    >
      <ol data-testid='list-container'>
        {React.Children.map(children, (child, i) => (i < children.length - 1
          ? child
          : cloneElement(child, { active: true })))}
      </ol>
    </NavContainer>
  )
}

DSBreadcrumb.Item = DSBreadcrumbItem

DSBreadcrumb.propTypes = {
  containerProps: PropTypes.shape({}),
  /**
   * Type of element you want the main container to be:
   * 'nav', 'div', 'header', 'footer', 'main'
   */
  as: PropTypes.string,
  label: PropTypes.string,
  /**
   * Container items to wrap
   */
  children: PropTypes.arrayOf(DSBreadcrumbItem).isRequired,
}

export { DSBreadcrumbItem }
export default DSBreadcrumb
