import React from 'react'
import { isFunction } from '@elliemae/ds-utilities/utils'
import { aggregatedClasses } from '@elliemae/ds-classnames'
import DSBreadcrumb from '@elliemae/ds-basic/Breadcrumb'
import ChevronLeft from '@elliemae/ds-icons/ChevronLeft'
import { DSIconSizes } from '@elliemae/ds-basic/Icon'
import EditableTitle from './EditableTitle'
import Title from './Title'
import DropdownTitle from './DropdownTitle'


const blockName = 'page-header'

const PageHeaderContainer = aggregatedClasses('div')(blockName, null, ({ withBottomSeparator, subtitle }) => ({
  'with-bottom-separator': withBottomSeparator,
  'with-subtitle': subtitle,
}))

const PageHeaderMainSection = aggregatedClasses('div')(`${blockName}-main-section`)
const PageHeaderTitleSection = aggregatedClasses('div')(`${blockName}-title-section`)
const PageHeaderToolbar = aggregatedClasses('div')(`${blockName}-toolbar`,
  null, () => ({ 'ie-flex-basis-auto': true }))
const PageHeaderBreadcrumb = aggregatedClasses(DSBreadcrumb)(`${blockName}-breadcrumb`)

const PageHeaderTitle = aggregatedClasses('div')(`${blockName}-title`)
const Subtitle = aggregatedClasses('div')(`${blockName}-title`, 'subtitle')
const BackArrow = aggregatedClasses(ChevronLeft)(`${blockName}-backarrow`)

function HeaderTitle({
  title = '',
  options = [],
  optionsMinWidth,
  onSelectOption = () => null,
  selection,
  subtitle = '',
  editable = false,
  editing = false,
  titleInputProps,
  zIndex,
}) {
  const titleComponent = editable
    ? (
      <EditableTitle
        editing={editing}
        titleInputProps={titleInputProps}
      >
        {title}
      </EditableTitle>
    )
    : <Title>{title}</Title>

  return (
    <PageHeaderTitle>
      {options.length
        ? (
          <DropdownTitle
            onSelectOption={onSelectOption}
            options={options}
            optionsMinWidth={optionsMinWidth}
            selection={selection}
            titleComponent={titleComponent}
            zIndex={zIndex}
          />
        )
        : titleComponent
      }
      {subtitle && <Subtitle>{subtitle}</Subtitle>}
    </PageHeaderTitle>
  )
}

function PageHeaderImpl({
  containerProps,
  className,
  title,
  titleOptions,
  renderToolbar,
  breadcrumbs = [],
  previousPage,
  onGoToPreviousPage = () => null,
  subtitle = '',
  withBottomSeparator,
  onSelectOption,
  optionsSelection,
  optionsMinWidth,
  editable = false,
  titleInputProps,
  editing = undefined,
  zIndex,
}) {
  const hasBreadcrumbs = !!breadcrumbs.length

  return (
    <PageHeaderContainer
      {...containerProps}
      className={className}
      classProps={{
        withBottomSeparator,
        subtitle,
      }}
    >
      { previousPage && (
      <BackArrow
        data-testid='back-arrow'
        onClick={onGoToPreviousPage}
        size={DSIconSizes.M}
      />
      )}
      <PageHeaderMainSection>
        { hasBreadcrumbs && (
        <PageHeaderBreadcrumb>
          {breadcrumbs.map(breadcrumb => (
            <DSBreadcrumb.Item
              key={breadcrumb.label}
              {...breadcrumb}
            />
          ))}
        </PageHeaderBreadcrumb>
        )}
        { previousPage && (
        <DSBreadcrumb.Item
          key={previousPage.id}
          label={previousPage.title}
          onClick={previousPage.onClick}
          {...previousPage}
        />
        )}
        <PageHeaderTitleSection>
          <HeaderTitle
            editable={editable}
            editing={editing}
            onSelectOption={onSelectOption}
            options={titleOptions}
            optionsMinWidth={optionsMinWidth}
            selection={optionsSelection}
            subtitle={subtitle}
            title={title}
            titleInputProps={titleInputProps}
            zIndex={zIndex}
          />
          { isFunction(renderToolbar) && (
          <PageHeaderToolbar>
            { renderToolbar() }
          </PageHeaderToolbar>
          )}
        </PageHeaderTitleSection>
      </PageHeaderMainSection>
    </PageHeaderContainer>
  )
}


export default PageHeaderImpl
