import React, { useState } from 'react'
import PropTypes from 'prop-types'
import { compact } from 'lodash'
import { Grid } from '@elliemae/ds-basic/Grid'
import styled from 'styled-components'
import theme from '@elliemae/ds-system/theme'
import { PageHeaderTitle } from './components/PageHeaderTitle/PageHeaderTitle'
import { PageHeaderEditableTitle } from './components/PageHeaderEditableTitle/PageHeaderEditableTitle'
import { PageHeaderSummary } from './components/PageHeaderSummary/PageHeaderSummary'
import { PageHeaderMenu } from './components/PageHeaderMenu/PageHeaderMenu'
import { PageHeaderToolbar } from './components/PageHeaderToolbar/PageHeaderToolbar'
import { PageHeaderBack } from './components/PageHeaderBack/PageHeaderBack'
import { cssPrefix } from './components/blocks'

const PageHeaderCustomRenderer = styled.div`
  width: auto;
  color: ${theme.colors.brand[800]};
  font-style: italic;
  font-size: 0.8rem;
`

const Container = styled.div`  
  width: 100%;
  margin-bottom: ${theme.space[1]};
`
const ContainerWBorder = styled.div`
  width: 100%;
  padding-bottom: ${theme.space[1]};
  border-bottom: solid 1px ${theme.colors.neutral[400]};
`
const getActiveEditing = editing => (editing ? {
  opacity: 0.5,
  pointerEvents: 'none',
} : {})

export const DSPageHeaderV2 = ({
  containerProps = {},
  breadcrumbs = null,
  pageTitle = null,
  backarrow = null,
  summary = null,
  toolbar = null,
  menu = null,
}) => {
  const wrapperRow = ['auto', 1]
  const mainRow = compact([1, '268px'])
  const pageTitleRow = compact(['auto', menu && '30px', summary && 'auto', 1])
  const [activeEditing, setActiveEditing] = useState(false)
  const activeEditingStyle = getActiveEditing(activeEditing)
  return (
    <Container {...containerProps}>
      <ContainerWBorder>
        <Grid cols={wrapperRow}>
          <Grid>
            <PageHeaderBack style={activeEditingStyle}>
              {backarrow}
            </PageHeaderBack>
          </Grid>
          <Grid rows={['auto', 'auto']}>
            <Grid className={`${cssPrefix}-page-header-v2-breadcrumb`} style={activeEditingStyle}>
              {breadcrumbs}
            </Grid>
            <Grid cols={mainRow}>
              <Grid cols={pageTitleRow}>
                {React.cloneElement(pageTitle, {
                  onBlur: (e) => {
                    setActiveEditing(false)
                    if (pageTitle.props.onBlur) pageTitle.props.onBlur(e)
                  },
                  onFocus: (e) => {
                    setActiveEditing(true)
                    if (pageTitle.props.onFocus) pageTitle.props.onFocus(e)
                  },
                })}
                {menu && (
                  <PageHeaderMenu>
                    {menu}
                  </PageHeaderMenu>
                )}
                {summary}
                <div />
              </Grid>
              <Grid>
                <PageHeaderToolbar>
                  {toolbar}
                </PageHeaderToolbar>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </ContainerWBorder>
    </Container>
  )
}

DSPageHeaderV2.propTypes = {
  containerProps: PropTypes.shape({}),
}

export {
  PageHeaderTitle,
  PageHeaderEditableTitle,
  PageHeaderSummary,
  PageHeaderMenu,
  PageHeaderCustomRenderer,
}
