import React from 'react'
import DSTruncatedTooltipText from '@elliemae/ds-basic/TruncatedTooltipText'
import RequiredMark from '@elliemae/ds-basic/form/RequiredMark'
import { PageHeaderTitleWrapper } from '../blocks'

export const PageHeaderTitle = ({ children, showMarker = false, ...rest }) => (
  <PageHeaderTitleWrapper {...rest}>
    <DSTruncatedTooltipText value={children} />
    {showMarker && <RequiredMark required />}
  </PageHeaderTitleWrapper>
)
